@import "../../style/variables";
@import "../../style/mixins";

.select {
  &__window {
    border-color: $main-color;
    color: $main-color;
    height: 30px;
    cursor: pointer;
    
    &:hover {
      border-color: $border-color;
    }
  }

  &__label {
   color: $main-color; 
   text-transform: uppercase;
   display: flex;
   flex-direction: column;
   gap: 5px;
   align-items: flex-end;
 }
}
